p,h1,h2,a, .pol-pri{
    font-family: Arial, sans-serif;
}
h1 {
    text-align: center;
}
.ctn-politicas {
    width: 100%;
    height: 100vh;
}
.ctn-politicas h1 {
    margin-top: 60px;
    margin-bottom: 30px;
}
.ctn-politicas p {
    text-align: justify;
}
.ctn-contenido {
    display: flex;
    justify-content: center;
    height: 100%;
    align-items: center;
}
.ctn-contenido p, 
.ctn-contenido a {
    font-size: 20px;
    margin-right: 20px;
    margin-bottom: 0;
}
/* .resaltado {
    color: #C50909;
    font-size: 43px;
} */
.txt-term,
.lista-term li, 
.resalt{
    font-size: 20px;
}
@media only screen and (max-width: 500px) {
    .ctn-politicas {
        width: 90%;
        padding-bottom: 40px;
    }
    .resaltado {
        font-size: 33px;
    }
    .titulo-term{
        font-size: 30px;
    }
    .titulo-dos-term {
        font-size: 25px;
    }
}